@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
  font-size: 0.75em;
  color:#dfdff5;
}
#first, #second, #third, #fourth, #fifth, #last{
  height:200vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 1200px;
	min-width: 700px;
  margin: 0 auto;

}
#first, #last{
  height: 100vh;
}
.title{
  font-size: 64px;
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */

}
.description{
  
  display: -webkit-flex;
  
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.description p{
  
  font-size: 24px;
  display: -webkit-flex;
  display: flex;
  /* justify-content: center; */
  margin:10px;
}

#sections{
  z-index: 5;
}

.timeline-wrapper{
  background: #e1e1e1;
  max-width: 1200px;
	min-width: 420px;
  margin: 0 auto;
	padding: 20px 10px;
  color:#222;
  width:100vw;
}

.timeline-chart{
  display: grid;
	border-radius: 4px;
	overflow: scroll;
	box-sizing: border-box;
  
}

.timeline-range{
  background: #efefef;
  display:grid;
  grid-template-columns: 150px repeat(6, 1fr);
}

.timeline-range span{
    text-align: center;
    -webkit-align-self: center;
            align-self: center;
    padding: 10px 0;
  
}


.timeline-items{
  display:-webkit-flex;
  display:flex;
}
.timeline-item{
  background: #ededed;
  border-radius: 4px;
  padding:4px;
  margin:10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: stretch;
          align-items: stretch;
  min-width: 300px;
  max-width: 360px;

}

.timeline-item-content{
  padding:12px;
}
.timeline-item-content .name{
  padding:8px;
  border-radius: 4px;
  background: rgb(200, 200, 200);
  
}

.timeline-item-content .item-id{
  padding:8px;
  border-radius: 4px;
  margin-right:2px;
  background: rgb(200, 200, 200);
}




.timelineStart{
  background: #cdcdcd;
  padding:4px 10px;
  margin:4px 10px;
  border-radius: 4px;
}

.timelineEnd{
  background: #cdcdcd;
  padding:4px 10px;
  margin:4px 10px;
  border-radius: 4px;
}

#logo-pattern{
  width:100%;
  height:100%;
  position: fixed;
  top:0;
  left:0;
  opacity: 0.05;
  z-index: -5;
 
}
#pattern-fill{
  width: 100%;
  height: 100%;
  
}
#down-arrow{
  width:100px;
  height:100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-self: center;
          align-self: center;
  padding-top:40px;

}

/* threejs */
#three{
  background: #17161A;
  position: fixed;
  top:0px;
  left:0px;
  z-index: -10;
}
